import React from "react"
import Link from "gatsby-link"
import styled from "styled-components"
import Helmet from "react-helmet"
import Fade from "react-reveal/Fade"
import {
  TINY_SHADOW,
  TINY_BKG,
  SECONDARY_TEXT
} from "../../components/consts/colors.js"
import Icon from "../../components/Icons"
import "../../css/index.css"
import "../../css/typography.css"
import "../../css/Hide.css"
import SectionContainer from "../../components/SectionContainer"
import SectionContainerBackground from "../../components/SectionContainerBackground"
import SectionHeaderContent from "../../components/SectionHeaderContent"
import SectionContent from "../../components/SectionContent"
import {
  TinyButton,
  TinyButtonAlt,
  TinyButtonWhite
} from "../../components/Button.js"
import SectionImages from "../../components/SectionImages"
import SectionImage from "../../components/SectionImage"
import FAQHeader from "../../components/FAQHeader"
import designer from "../../assets/designer-view.png"

import withScreensize from "../../components/withScreensize"
import ScreensizeProvider from "../../components/ScreensizeProvider"
//dangerouslySetInnerHTML={{ __html: pageData.FAQCopy }}
const pageData = {
  FAQHeadline: "Easily upload your custom designs on selected products.",
  FAQCopy: (
    <p>
      From your <a href="https://my.tinymerch.com">TinyMerch Dashboard</a> you
      can easily add products to your store! We use DTG printing for fabric
      products, so avoid PNG files that include{" "}
      <a href="https://www.printful.com/transparency-in-dtg-files">
        semi-transparent values
      </a>. After uploading and placing your image, TinyMerch will generate
      high-resolution print files automatically.
      <br />
      <br />
      Finally, name your price! You can choose any profit amount above the base
      item's list price. Is it a $15 print? $50? $500? That's up to you! (And
      what your customers can afford 😜)
    </p>
  ),
  FAQLink: "#",
  FAQwhatIsCTA: "Get Started!"
}

const NextQ = styled.div`
  margin: 10px 0px;
  a {
    display: flex;
    transition: all 200ms ease;
    :hover {
      transform: translateX(3px);
    }
    div {
      padding-left: 10px;
      position: relative;
      top: 2px;
    }
  }
`

const DesigningProds = withScreensize(props => {
  const isMobile = props.screensize === "phones"
  const isTablet = props.screensize === "thatUglyInBetweenSize"
  const isSmall = props.screensize === "devWithConsoleOpen"
  const isWidescreen = props.screensize === "widescreenFools"

  return (
    <div>
      <Helmet title="TinyMerch | FAQ – What is TinyMerch" />
      <FAQHeader />
      <SectionContainer positionContent="center">
        <SectionContent
          justify="center"
          color
          height={
            isMobile
              ? "300px"
              : isTablet ? "400px" : isWidescreen ? "500px" : "500px"
          }
          width={
            isMobile ? "95%" : isTablet ? "80%" : isWidescreen ? "40%" : "50%"
          }>
          <Fade left delay={50} duration={400}>
            <h2>{pageData.FAQHeadline}</h2>
          </Fade>
          <Fade left delay={100} duration={400}>
            <p style={{ paddingTop: "25px" }}>{pageData.FAQCopy}</p>
          </Fade>
          <Fade left delay={150} duration={400}>
            <NextQ>
              <Link to="/faq/getting-paid">
                💸 Getting Paid
                <Icon name="forward" color="#6079F2" />
              </Link>
            </NextQ>
          </Fade>
          {/* <a href={pageData.FAQLink}>
            <TinyButton>{pageData.FAQwhatIsCTA}</TinyButton>
          </a> */}
        </SectionContent>
        <SectionImages
          width={
            isMobile ? "100%" : isTablet ? "100%" : isWidescreen ? "60%" : "50%"
          }
          height={
            isMobile
              ? "200px"
              : isTablet ? "300px" : isWidescreen ? "600px" : "600px"
          }>
          <Fade right delay={350} duration={500}>
            <SectionImage
              shadow
              src={designer}
              ImgWidth={
                isMobile
                  ? "300px"
                  : isTablet ? "500px" : isWidescreen ? "700px" : "600px"
              }
              positionTB={
                isMobile
                  ? "10px"
                  : isTablet ? "50px" : isWidescreen ? "80px" : "100px"
              }
              positionLR={
                isMobile ? "0%" : isTablet ? "0%" : isWidescreen ? "20%" : "10%"
              }
            />
          </Fade>
        </SectionImages>
      </SectionContainer>
    </div>
  )
})

export default props => (
  <ScreensizeProvider>
    <DesigningProds {...props} />
  </ScreensizeProvider>
)



